import PropTypes from "prop-types";
const Loading = () => {
  return (
    <>
      <div className="items-center justify-center text-center">
        <div
          className="inline-block h-6 w-6 m-4 animate-spin rounded-full border-4 border-solid border-current border-r-transparent align-[-0.125em] motion-reduce:animate-[spin_1.5s_linear_infinite]"
          role="status"
        >
          <span className="!absolute !-m-px !h-px !w-px !overflow-hidden !whitespace-nowrap !border-0 !p-0 ![clip:rect(0,0,0,0)]">
            Loading...
          </span>
        </div>
        
      </div>
    </>
  );
};
Loading.propTypes = {
    text: PropTypes.string}
export default Loading;